<template>
  <section id="page-title">

    <img :src="require(`@/assets/${imageName}`)" alt="Image representing page title" :class="{ 'custom-fade-out': !!ytVideoId }" />
    <VideoComponent v-if="!!ytVideoId" :yt-video-id="ytVideoId" :start-time="startTime" :end-time="endTime" />

    <h1 v-if="!!title">
      <span v-html="title" />
    </h1>
    <a @click="scrollDown()">
      <span class="arrow"></span>
      Scroll
    </a>

    <div class="shaddow-overlay top"></div>
    <div class="shaddow-overlay bottom"></div>
  </section>
</template>

<script>
const VideoComponent = () => import(/* webpackChunkName: "video" */ '@/components/VideoComponent.vue')

export default {
  name: 'PageTitle',

  components: {
    VideoComponent
  },

  props: {
    imageName: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    ytVideoId: {
      type: String,
      default: ''
    },
    startTime: {
      type: Number,
      default: 0
    },
    endTime: {
      type: Number,
      default: 0
    }
  },

  methods: {
    scrollDown () {
      document.querySelectorAll('section')[1].scrollIntoView({
        behavior: 'smooth',
        block: 'start',
        inline: 'nearest'
      })
    }
  }
}
</script>

