<template>
  <div id="mob-menu">
    <div class="mob-bar" :class="[$mq, { 'scrolled-bar' : !isTop }]">
      <div class="logo-container">
        <a href="/" rel="noopener">
          <img :src="require('@/assets/logo.png')" :width="`${isTop ? 100 : 80}px`" :height="`${isTop ? 80 : 65}px`" alt="Website logo" />
        </a>
      </div>
      <div
        id="menu-icon"
        :class="{ 'open' : isOpened }"
        @click="menuToggle(!isOpened)"
      >
        <span v-for="span in 4" :key="span"></span>
      </div>
    </div>
    <nav class='nav' :class="[isOpened ? 'open' : 'close', { 'scrolled-nav' : !isTop }]">
      <router-link @click.native="menuToggle(false)" to="/">Home</router-link>
      <router-link @click.native="menuToggle(false)" to="/about">About</router-link>
      <router-link @click.native="menuToggle(false)" to="/resume">Resume</router-link>
      <router-link @click.native="menuToggle(false)" to="/contact">Contact</router-link>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'MobNavigation',

  data () {
    return {
      isOpened: false,
      isTop: true
    }
  },

  mounted () {
    this.listenForScroll()
  },

  methods: {
    menuToggle (open) {
      this.isOpened = open
    },
    listenForScroll () {
      window.onscroll = () => {
        this.isTop = Math.max(window.pageYOffset, document.documentElement.scrollTop, document.body.scrollTop) === 0
      }
    }
  }

}
</script>

