    border-radius: 9px;
    transform: rotate(0deg);
    transition: .25s ease-in-out;
    &:nth-child(1) {
      top: 0px;
    }
    &:nth-child(2),
    &:nth-child(3) {
      top: 14px;
    }
    &:nth-child(4) {
      top: 28px;
    }
  }
  &.open {
    span {
